	module.exports = {"data":{"markdownRemark":{"html":"<h2>Float and Clear</h2>\n<p>The CSS <code>float</code> property specifies how an element should float.</p>\n<p>The CSS <code>clear</code> property specifies what elements can float beside the cleared element and on which side.</p>\n<h3>The <code>float</code> Property</h3>\n<p>The <code>float</code> property is used for positioning and layout on web pages.</p>\n<p>The <code>float</code> property can have one of the following values:</p>\n<p><code>left</code> - The element floats to the left of its container\n<code>right</code>- The element floats to the right of its container\n<code>none</code> - The element does not float (will be displayed just where it occurs in the text). This is default\n<code>inherit</code> - The element inherits the float value of its parent\nIn its simplest use, the <code>float</code> property can be used to wrap text around images.</p>\n<h4>Float in Picture:</h4>\n<p><img src=\"https://github.com/jamal-pb95/guides/blob/master/assets/css3-float-print-layout.png\" alt=\"float image for print layout\" title=\"css-tricks-float-img\"></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>img {\n    float: right;\n}</code></pre>\n      </div>\n<p>This example specifies that an image should float to the right in a page:</p>\n<p><img src=\"https://github.com/jamal-pb95/guides/blob/master/assets/css3-float-web-text-wrap.png\" alt=\"Float image for web layout\" title=\"float img web\"></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>img {\n    float: left;\n}</code></pre>\n      </div>\n<p>This example specifies that an image should float to the left in a page:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>img {\n    float: none;\n}</code></pre>\n      </div>\n<p>In the following example the image will be displayed just where it occurs in the text (<code>float: none;</code>):</p>\n<h3>The <code>clear</code> Property</h3>\n<p>The <code>clear</code> property specifies what elements can float beside the cleared element and on which side.</p>\n<p>The <code>clear</code> property can have one of the following values:</p>\n<p><code>none</code> - Allows floating elements on both sides. This is default\n<code>left</code> - No floating elements allowed on the left side\n<code>right</code>- No floating elements allowed on the right side\n<code>both</code> - No floating elements allowed on either the left or the right side\n<code>inherit</code> - The element inherits the clear value of its parent\nThe most common way to use the <code>clear</code> property is after you have used a <code>float</code> property on an element.</p>\n<p>When clearing floats, you should match the <code>clear</code> to the <code>float</code>. If an element is floated to the <code>left</code>, then you should <code>clear</code> to the <code>left</code>. Your floated element will continue to <code>float</code>, but the cleared element will appear below it on the web page.</p>\n<h4>Example:</h4>\n<p><img src=\"https://github.com/jamal-pb95/guides/blob/master/assets/unclearedfooter.png\" alt=\"unclear footer image\" title=\"unclear footer image\">\nSource: CSS-TRICS</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>div {\n    clear: left;\n}</code></pre>\n      </div>\n<p><img src=\"https://github.com/jamal-pb95/guides/blob/master/assets/clearedfooter.png\" alt=\"clear footer image\" title=\"clear footer image\">\nSource: CSS-TRICS</p>\n<h3>Additional Resources:</h3>\n<ul>\n<li>MDN CSS: <a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/float\">Float</a> &#x26; <a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/clear\">Clear</a></li>\n<li><a href=\"https://www.w3schools.com/css/css_float.asp\">W3Schools tutorials</a></li>\n<li>CSS-Tricks: <a href=\"https://css-tricks.com/all-about-floats/\">float</a> &#x26; <a href=\"https://css-tricks.com/almanac/properties/c/clear/\">clear</a></li>\n</ul>","fields":{"slug":"/css/layout/float-and-clear/"},"frontmatter":{"title":"Float and Clear"}}},"pathContext":{"slug":"/css/layout/float-and-clear/"}}

